.footer{
    width: 100vw;
    
    .attribution{
        position: absolute;
        left: 50%;
        font-size: 1.1rem;
        transform: translate(-50%);
        bottom: 1rem;
        .name{
            color: rgb(156, 191, 91);
            text-decoration: none;
        }
    }

    button{
        position: absolute;
        bottom: 1rem;
        padding: 7px 25px;
        right: 1rem;
        border-radius: .4rem;
        border: 3px solid $header-outline;
        color: lighten($header-outline,20%);
        background: none;
        font-weight: 600;
        letter-spacing: 2px;

        &:hover{
            background-color: rgba($color: #ffffff, $alpha: .8);
            color: $dark-text;
            cursor: pointer;
        }
    }
}